@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,?300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swaphttps://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap" rel="stylesheet);
:root{
  --font-base: 'Montserrat', sans-serif;
  --gold-color: #D7BC83;
  --black-color: #1E1E1E;
  --grey-color: #8e8e8e;
  --white-color: #fff;
  --whitesmoke-color: #f5f5f5;
  --transition: all 300ms ease-in-out;
}

*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

html{
  font-size: 10px;
}

body{
  font-size: 1.8rem;
  line-height: 1.6;
  font-family: var(--font-base);
  color: var(--black-color);
}

a{
  color: unset;
  text-decoration: none;
}

ul{
  list-style-type: none;
}

img{
  width: 100%;
  display: block;
}

button{
  cursor: pointer;
  outline: 0;
  border: none;
  background-color: transparent;
  font-family: inherit;
  font-size: 1.8;
}

input, textarea{
  outline: 0;
  border: none;
  resize: none;
}
.container{
  max-width: 1177px;
  margin: 0 auto;
  padding: 0 2rem;
}

.section__padding{
  padding: 10rem 0;
}

.flex{
  display: flex;
}

.flex__center{
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid{
  display: grid;
}

/* section*/
.section__title{
  margin-bottom: 4rem;
  color: var(--black-color);
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 4rem;
}

.section__padding{
  line-height: 1.7;
  font-weight: 300;
  opacity: 0.95;
  letter-spacing: 1px;
}

.text__center{
  text-align: center;
}

.text__upper{
  text-transform: uppercase;
}

.text__cap{
  text-transform: capitalize;
}

.text__gold{
  color: var(--gold-color);
}

.text__light{
  color: var(--white-color)!important;
}

.text__gray{
  color: var(--gray-color);
}

/*fonts*/
.fw__2{font-weight: 200;}
.fw__3{font-weight: 300;}
.fw__4{font-weight: 400;}
.fw__5{font-weight: 500;}
.fw__6{font-weight: 600;}
.fw__7{font-weight: 700;}
.fw__8{font-weight: 800;}

/*bgs and colors*/
.bg__gold{
  background-color: var(--gold-color);
}

.bg__dark{
  background-color: var(--black-color);
}

.bg__white{
  background-color: var(--white-color);
}

.bg__light{
  background-color: var(--white-color);
}

.bg__whitesmoke{
  background-color: var(--whitesmoke-color);
}

.bg__gray{
  background-color: var(--gray-color);
}

/*btn*/
.btn{
  display: inline-block;
  min-height: 54px;
  line-height: 54px;
  padding: 0 4.7rem;
  color: var(--white-color);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.5rem;
  font-weight: bolder;
  transition: var(--transition);
}

.btn__gold{
  background-color: var(--gold-color);
}

.btn__gold:hover{
  background-color: var(#a89468);
}

/*letter spacing*/
.ls__1{
  initial-letter: 1px;
}

.ls__2{
  initial-letter: 2px;
}


.header{
    min-height: 20vh;
}
.header__content{
 min-height: calc(100vh - 106px);
 flex-direction: column;
}
.header__title{
 font-size: 50px;
 line-height: 1.2;
 color: var(--white-color);
}
.header__title2{
 font-size: 2.0rem;
 line-height: 1.2;
 color: var(--gold-color);
}
.header__content .para__text{
 padding: 6.2rem 0;
 max-width: 100px;
 margin: 0 auto;
}
.btn{
 margin: 15px;
}

@media(min-width: 992px){
 .header__content{
     flex-direction: row;
 }
 .header__title{
  font-size: 3.6rem;
 }
 .header__content--right{
     text-align: left;
     width: 50%;
 }
 .header__content--left{
     width: 50%;
 }
 .header__content--right .para__text{
     margin-left: 0;
 }
}
.navbar{
 padding: 3.8rem 0;
}
.navbar__brand img{
 width: 150px;
}
.brand__and__toggler{
 align-items: center;
 justify-content: space-between;
 width: 100%;
}
.navbar__collapse{
 display: none;
}
.navbar__collapse .nav__link{
 letter-spacing: 1px;
 transition: var(--transition);
}
.navbar__collapse .nav__link:hover, .nav__link{
 color: var(--gold-color);
}

/* smallscreen */
.navbar__smallscreen{
 position: fixed;
 right: 0;
 top: 0;
 background-color: var(--gold-color);
 height: 100%;
 width: 100%;
 padding: 3rem;
 box-shadow: -3px 0 14px 0 rgba(0, 0, 0, 0.4);
 z-index: 999;
 transition: var(--transition);
}
.navbar__close--btn{
 position: absolute;
 right: 2rem;
 top: 2rem;
 cursor: pointer;
 transition: var(--transition);
}
.navbar__close--btn:hover{
 opacity: 0.9;
}
.navbar__smallscreen .nav__link{
 transition: var(--transition);
 letter-spacing: 1px;
 font-size: 1.8rem;
}
.navbar__smallscreen .nav__link:hover{
 padding-left: 1rem;
}
.navbar__smallscreen .nav__item{
 margin: 2rem 0;
}
@media(min-width: 578px){
 .navbar__smallscreen{
     width: 300px;
 }
}

@media(min-width: 992px){
 .navbar__open--btn{
     display: none;
 }
 .navbar__collapse{
     display: flex;
 }
 .navbar__collapse .navbar__nav{
     display: flex;
     align-items: center;
 }
 .navbar__collapse .nav__item{
     margin-left: 3.8rem;
 }
 .navbar__smallscreen{
     display: none;
 }
}
.footer__navlink{
 font-size: 2rem;
 display: block;
 margin-bottom: 1rem;
}
.footer__content{
 grid-row-gap: 5rem;
 row-gap: 5rem;
}
.footer__title{
 margin-bottom: 1.6rem;
}
.footer__links li{
 margin-bottom: 1rem;
}
.footer__links li a{
 transition: var(--transition);
}
.footer__links li a:hover{
 color: var(--robin-blue-color);
}

@media screen and (min-width: 768px){
 .footer__content{
     grid-template-columns: repeat(2, 1fr);
     grid-column-gap: 4rem;
     column-gap: 4rem;
 }
}

@media screen and (min-width: 992px){
 .footer__content{
     grid-template-columns: repeat(3, 1fr);
 }
}

@media screen and (min-width: 1200px) {
 .footer__content{
     grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
     text-align: left;
 }
}
.wrapper{
 margin: 250px auto;
 width: 70%;
}
.pred__img{
 max-width: 320px;
 float: left;
 margin-right: 55px;
}
.text-box{
 margin-top: 150px;
}
.text-box h2 {
 font-size: 42px;
 color: var(--gold-color);
}
.text-box p{
 font-size: 16px;
 margin: 15px;
 text-align: justify;
}

.about{
 font-size: 1.7rem;
 font-family: inherit;
 padding: 1.2rem 2rem;
 width: 100%;
 margin: 1.6rem 0;
}
.work__content--item{
 position: relative;
}
.work__content--item::after{
 content: "";
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 background-color: rgba(0, 0, 0, 0.8);
 opacity: 0;
 transition: var(--transition);
}
.work__content--item .icon{
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 opacity: 0;
 transition: var(--transition);
}
.work__content--item .icon img{
 width: 40px;
}
.work__content--item:hover .icon{
 opacity: 1;
 z-index: 1;
}
.work__content--item:hover::after{
 opacity: 1;
}

@media screen and (min-width: 678px){
 .work__content{
     grid-template-columns: repeat(2, 1fr);
 }
}

@media screen and (min-width: 800px){
 .work__content{
     grid-template-columns: repeat(3, 1fr);
 }
}

@media screen and (min-width: 1200px){
 .work__content{
     grid-template-columns: repeat(4, 1fr);
 }
}

.image__modal{
 position: fixed;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 background-color: rgba(0, 0, 0, 0.8);
 z-index: 5;
 transition: var(--transition);
 visibility: hidden;
 opacity: 0;
}

.image__modal__show{
 visibility: visible;
 opacity: 1;
}
.image__modal--content{
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 max-width: 720px;
 min-width: 350px;
 width: 100%;
}
.image__modal--content img{
 width: 100%;
}
.modal__close--btn{
 position: absolute;
 right: -15px;
 top: -15px;
 border-radius: 50%;
 cursor: pointer;
 transition: var(--transition);
}
.modal__close--btn:hover{
 color: var(--dark-color)!important;
}
.contact__content{
 margin-top: 3.6rem;
}
.contact .para__text{
 max-width: 600px;
 margin: 0 auto;
}
.form__control{
 font-size: 1.7rem;
 font-family: inherit;
 padding: 1.2rem 2rem;
 width: 100%;
 margin: 1.6rem 0;
}
.form__submit--btn{
 display: block;
 margin: 2rem auto 0 auto;
}


@media screen and (min-width: 678px){
 .form__elem.form__elem--2{
     display: grid;
     grid-template-columns: repeat(2, 1fr);
     grid-column-gap: 3.2rem;
     column-gap: 3.2rem;
 }
}

@media screen and (min-width: 768px){
 .contact__content form{
     max-width: 680px;
     margin: 0 auto;
 }
}
